body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App{
  height: 100vh;
  width: 100vw;
}

.grid-container{
  position: fixed;
  display: inline-grid;
  grid-template: "auto auto auto auto auto";
}

.position-0{
  grid-row: 1;
  grid-column: 1;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.position-1{
  grid-row: 1;
  grid-column:2; 
  -webkit-transform: rotate(180deg); 
          transform: rotate(180deg);
}
.position-2{
  grid-row: 1;
  grid-column: 3;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.position-3{
  grid-row: 1;
  grid-column: 4;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.position-4{
  grid-row: 1;
  grid-column: 5;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.position-5{
  grid-row: 2;
  grid-column: 5;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.position-6{
  grid-row: 3;
  grid-column: 5;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.position-7{
  grid-row: 4;
  grid-column: 5;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
  .position-8{
    grid-row: 5;
    grid-column: 5;
  }
  .position-9{
    grid-row: 5;
    grid-column: 4;
  }
  .position-10{
    grid-row: 5;
    grid-column: 3;
  }
  .position-11{
    grid-row: 5;
    grid-column: 2;
  }
  .position-12{
    grid-row: 5;
    grid-column: 1;
  }
  .position-13{
    grid-row: 4;
    grid-column: 1;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  .position-14{
    grid-row: 3;
    grid-column: 1;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  .position-15{
    grid-row: 2;
    grid-column: 1;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}


